            let sindex = 0;
            if (clist && clist !== null) {
              // console.log(clist);
              clist.forEach((value, index) => {
                // console.log(index);
                if (index == 0) {
                  list[index] = h(
                    "div",
                    {
                      style: {
                        width: "80px",
                        display: "inline",
                        textAlign: "center",
                        // border: "1px solid #464c5b",
                        borderRadius: "5px",
                        margin: "0 auto"
                      },
                      domProps: {
                        title: params.row.plateNos
                      }
                    },
                    value
                  );
                } else if (index == 1) {
                  list[index] = h(
                    "div",
                    {
                      style: {
                        width: "80px",
                        display: "inline",
                        textAlign: "center",
                        // border: "1px solid #464c5b",
                        borderRadius: "5px",
                        margin: "0 auto"
                      },
                      domProps: {
                        title: params.row.plateNos
                      }
                    },
                    " / " + value
                  );
                } else if (index == 2) {
                  list[index] = h("span", [
                    h(
                      "span",
                      {
                        style: {
                          fontSize: "20px",
                          cursor: "pointer",
                          display: "inline"
                        },
                        on: {
                          click: () => {
                            this.showEllipsis(params.row.id);
                          }
                        }
                      },
                      "..."
                    ),
                    h(
                      "div",
                      {
                        style: {
                          fontSize: "10px",
                          cursor: "pointer",
                          display: "inline",
                          // border: "1px solid red",
                          marginBottom: "20px"
                        },
                        on: {
                          click: () => {
                            this.showEllipsis(params.row.id);
                          }
                        }
                      },
                      clist.length
                    )
                  ]);
                }
              });
            }
            return list;
          }
        },
        {
          title: "状态",
          align: "center",
          key: "cameraOnlineStatus",
          minWidth: 80,
          render: (h, params) => {
            console.log(params.row.cameraOnlineStatus);
            let state = params.row.cameraOnlineStatus;
            return h(
              "label",
              {
                style: {
                  color: params.row.cameraOnlineStatus == 1 ? "green" : "red"
                }
              },
              state === 0 ? "离线" : state === 1 ? "在线" : ""
            );
