                  open: () => h("span", "正常"),
                  close: () => h("span", "禁用")
                },
                on: {
                  "on-change": state => {
                    if (state) {
                      this.enableParking(parking.id);
                    } else {
                      this.disabledParking(parking.id);
                    }
                  }
                }
              }),
              h(
                "span",
                {
                  style: {
                    size: 26,
                    cursor: "pointer",
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.edit(parking.id);
                    }
                  }
                },
                "编辑"
              ),
              h(
                "span",
                {
                  style: {
                    size: 26,
                    cursor: "pointer"
                  },
                  on: {
                    click: () => {
                      this.delete(parking.id);
                    }
                  }
                },
                "删除"
              )
            ]);
            // }
            if (params.row.type != 0) {
              return menu;
            }
          }
        }
