  },
  mounted() {
    this.$root.event.$on("logout", res => {
      // console.log(res)
      this.show = res;
    });
  },
  methods: {
    //车位名称
    floores() {
      axios
        .request({
          url: url.url.car_name,
          method: "get"
        })
        .then(res => {
          console.log(res);
          this.carnames = res.data.data;
        });
    },
    //新增一体机
    add() {
      console.log("新增一体机");
      this.modules = true;
    },
    // 弹框编辑
    modal_edit(id) {
      console.log("编辑");
      this.$router.push({
        path: "/one_administration_edit",
        query: {
          id: id
        }
      });
    },
    //弹框删除
    modal_del(id) {
      console.log(id);
      this.show_delete = true;
      this.id_delete = id;
      this.deletes = 2;
    },
    //弹框返回
    module_back() {
      this.modules = false;
    },
    //弹框增加
    modal_add() {
      console.log("增加");
      this.$router.push({
        path: "/one_administration_edit"
      });
    },
    //弹框查询
    sear1() {
      let that = this;
      this.loadinges1 = true;
      console.log("查询");
      that.loadingText1 = "数据加载中...";
      this.data1 = "";
      this.condition1.pageNo = 1;
      if (this.modal_onename) {
        this.condition1.condition.cameraCode = this.modal_onename;
      } else {
        this.condition1.condition.cameraCode = null;
      }
      console.log(this.condition1);
      axios1
        .request({
          url: url.url.search_park,
          data: this.condition1,
          method: "post"
        })
        .then(res => {
          console.log(res);
          that.spinShow = false;
          if (res.data.data.list.length == 0) {
            this.loadingText1 = "";
          } else {
            this.data1 = res.data.data;
            this.totalPages1 = res.data.data.totalPages;
          }
          this.numbers1 = 1;
          this.loadinges1 = false;
        });
    },
    //table列表
    list() {
      let that = this;
      this.condition.pageNo = 1;
      this.data = "";
      this.totalPages = 0;
      axios1
        .request({
          url: url.url.search_park,
          data: this.condition,
          method: "post"
        })
        .then(res => {
          console.log(res);
          that.spinShow = false;
          if (res.data.data.list.length == 0) {
            this.loadingText = "";
          } else {
            this.data = res.data.data;
            this.totalPages = res.data.data.totalPages;
          }
          this.loadinges = false;
        });
    },
    list1() {
      let that = this;
      this.condition1.pageNo = 1;
      this.data1 = "";
      this.totalPages1 = 0;
      axios1
        .request({
          url: url.url.search_park,
          data: this.condition1,
          method: "post"
        })
        .then(res => {
          that.spinShow = false;
          if (res.data.data.list.length == 0) {
            this.loadingText1 = "";
          } else {
            this.data1 = res.data.data;
            this.totalPages1 = res.data.data.totalPages;
          }
          this.loadinges1 = false;
        });
    },
    //显示省略车牌号
    showEllipsis(id) {
      console.log(id);
      this.data.list.map(res => {
        if (res.id == id) {
          if (res.edificeName) {
            this.module_ellipsis = res.edificeName;
            this.modal10 = true;
          }
        }
      });
    },
    //点击数字第几页
    search(data) {
      let that = this;
      this.data = "";
      this.loadinges = true;
      console.log(data);
      // this.pages = 0;
      this.pages = data;
      this.condition.pageNo = data;
      axios1
        .request({
          url: url.url.search_park,
          data: this.condition,
          method: "post"
        })
        .then(res => {
          console.log(res);
          this.numbers = data;
          if (res.data.data.list.length == 0) {
            this.loadingText = "";
          } else {
            this.data = res.data.data;
            this.totalPages = res.data.data.totalPages;
          }
          this.loadinges = false;
        });
    },
    //点击数字第几页
    search1(data) {
      let that = this;
      this.loadinges1 = true;
      console.log(data);
      this.pages = data;
      this.condition1.pageNo = data;
      axios1
        .request({
          url: url.url.search_park,
          data: this.condition1,
          method: "post"
        })
        .then(res => {
          console.log(res);
          that.spinShow = false;
          this.numbers1 = data;
          if (res.data.data.list.length == 0) {
            this.loadingText1 = "";
          } else {
            this.data1 = res.data.data;
            this.totalPages1 = res.data.data.totalPages;
          }
          this.loadinges1 = false;
        });
    },
    // 输入数字
    number(data) {
      if (data == "") {
        this.numbers = data;
      } else {
        var reg = /^\d{1,}$/;
        if (reg.test(data) == false) {
          this.numbers = 1;
        } else {
          if (data >= 1) {
            this.numbers = data;
          } else {
            this.numbers = 1;
          }
        }
      }
    },
    //输入数字
    number1(data) {
      if (data == "") {
        this.numbers1 = data;
      } else {
        var reg = /^\d{1,}$/;
        console.log(reg.test(data));
        if (reg.test(data) == false) {
          this.numbers1 = 1;
        } else {
          if (data >= 1) {
            this.numbers1 = data;
          } else {
            this.numbers1 = 1;
          }
        }
      }
    },
    //点击按钮跳转第几页
    jump() {
      let that = this;
      this.data = "";
      this.loadinges = true;

      if (this.numbers == "") {
        this.numbers = 1;
      } else if (this.numbers > this.totalPages) {
        this.numbers = this.totalPages;
      }
      this.condition.pageNo = this.numbers;
      axios1
        .request({
          url: url.url.search_park,
          data: this.condition,
          method: "post"
        })
        .then(res => {
          that.pages = Number(this.numbers);
          if (res.data.data.list.length == 0) {
            this.loadingText = "";
          } else {
            this.data = res.data.data;
          }
          this.loadinges = false;
        });
    },
    //点击按钮跳转第几页
    jump1() {
      let that = this;
      this.loadinges1 = true;
      if (this.numbers1 == "") {
        console.log("1111111111");
        this.numbers1 = 1;
      } else if (this.numbers1 > this.totalPages1) {
        console.log(this.totalPages1);
        this.numbers1 = this.totalPages1 == "" ? "1" : this.totalPages1;
      }
      this.condition1.pageNo = this.numbers1;
      axios1
        .request({
          url: url.url.search_park,
          data: this.condition1,
          method: "post"
        })
        .then(res => {
          console.log(res);
          that.pages1 = Number(this.numbers1);
          that.spinShow = false;
          if (res.data.data.list.length == 0) {
            this.loadingText1 = "";
          } else {
            this.data1 = res.data.data;
          }
          this.loadinges1 = false;
        });
    },
    del() {
      this.modal10 = false;
    },
    success() {
      this.show = false;
      axios
        .request({
          url: url.url.logout,
          method: "post"
        })
        .then(res => {
          console.log(res);
          if (res.data.logout == true) {
            sessionStorage.setItem("logout", 1);
            this.$router.replace({ path: "/" });
            sessionStorage.setItem("token", "");
          } else {
            this.$Notice.error({
              title: res.data.message,
              desc: ""
            });
          }
        });
    },
    cancel() {
      this.show = false;
    },
    //启用
    enableParking(id) {
      let data = {};
      console.log(id);
      axios1
        .request({
          url: url.url.on_off_camera,
          data: {
            id: id,
            cameraFlag: 1
          },
          method: "post"
        })
        .then(res => {
          console.log(res);
          if (res.data.code == 200) {
            this.$Notice.success({
              title: "系统提示",
              desc: res.data.message
            });
          } else {
            this.$Notice.error({
              title: "系统提示",
              desc: res.data.message
            });
          }

          console.log(res);
        });
    },
    //禁用
    disabledParking(id) {
      console.log(id);
      axios1
        .request({
          url: url.url.on_off_camera,
          params: {
            id: id,
            cameraFlag: 0
          },
          method: "post"
        })
        .then(res => {
          console.log(res);
          if (res.data.code == 200) {
            this.$Notice.success({
              title: "系统提示",
              desc: res.data.message
            });
          } else {
            this.$Notice.error({
              title: "系统提示",
              desc: res.data.message
            });
          }

          console.log(res);
        });
    },
    //编辑
    edit(id) {
      console.log(id);
      this.$router.push({
        path: "/one_administration_edit",
        query: {
          id: id
        }
      });
    },
    //取消删除
    canceles_delete() {
      this.show_delete = false;
      this.loading_delete = false;
      this.id_delete = "";
    },
    //删除
    delete(id) {
      this.show_delete = true;
      this.id_delete = id;
      this.deletes = 1;
      console.log("删除");
      console.log(id);
    },
    success_delete() {
      this.loading_delete = true;
      console.log(this.modules);
      if (this.modules == true) {
        console.log("走删除");
        axios
          .request({
            url: url.url.delete_camera,
            params: {
              id: this.id_delete
            },
            method: "delete"
          })
          .then(res => {
            console.log(res);
            if (res.data.code == 200) {
              this.$Notice.success({
                title: "系统提示",
                desc: res.data.message
              });
              if (this.deletes == 2) {
                this.list1();
              } else if (this.deletes == 1) {
                this.list();
              }
            } else {
              this.$Notice.error({
                title: "系统提示",
                desc: res.data.message
              });
            }
            this.deletes = "";
            this.loading_delete = false;
            this.show_delete = false;
          });
      } else {
        console.log("走清除");
        axios
          .request({
            url: url.url.clear_camera,
            params: {
              id: this.id_delete
            },
            method: "delete"
          })
          .then(res => {
            console.log(res);
            if (res.data.code == 200) {
              this.$Notice.success({
                title: "系统提示",
                desc: res.data.message
              });
              this.list1();
              this.list();
            } else {
              this.$Notice.error({
                title: "系统提示",
                desc: res.data.message
              });
            }
            this.deletes = "";
            this.loading_delete = false;
            this.show_delete = false;
          });
      }
    },
    //搜索
    sear() {
      let that = this;
      this.loadinges = true;
      console.log("查询");
      that.loadingText = "数据加载中...";
      this.data = "";
      this.condition.pageNo = 1;
      this.condition.pageSize = 10;
      if (this.carname) {
        this.condition.condition.parkCode = this.carname;
      } else {
        this.condition.condition.parkCode = null;
      }
      if (this.inout) {
        this.condition.condition.cameraType = this.inout;
      } else {
        this.condition.condition.cameraType = 0;
      }
      if (this.onename) {
        this.condition.condition.cameraCode = this.onename;
      } else {
        this.condition.condition.cameraCode = null;
      }
      if (this.search_state != null) {
        this.condition.condition.cameraOnlineStatus = this.search_state;
      } else {
        this.condition.condition.cameraOnlineStatus = null;
      }
      console.log(this.search_state);
      console.log(this.condition);
      axios1
        .request({
          url: url.url.search_park,
          data: this.condition,
          method: "post"
        })
        .then(res => {
          console.log(res);
          if (res.data.data.list.length == 0) {
            this.loadingText = "";
          } else {
            this.data = res.data.data;
            this.totalPages = res.data.data.totalPages;
          }
          this.numbers = 1;
          this.loadinges = false;
        });
    },
    //导出
    out() {
      let that = this;
      that.loading1 = true;
      this.condition.pageSize = 1000;
      axios1
        .request({
          url: url.url.search_park,
          data: this.condition,
          method: "post"
        })
        .then(res => {
          console.log(res);
          that.spinShow = false;
          if (res.data.data.list.length == 0) {
            this.loadingText = "";
            that.loading1 = false;
            this.$Notice.error({
              title: "系统提示",
              desc: "导出数据不能为空"
            });
          } else {
            let datas = res.data.data.list.map(res => {
              // console.log(res);
              res.cameraType =
                res.cameraType == 1
                  ? "入口"
                  : res.cameraType == 2
                  ? "出口"
                  : "";

              res.cameraOnlineStatus =
                res.cameraOnlineStatus == 0
                  ? "离线"
                  : res.cameraOnlineStatus == 1
                  ? "在线"
                  : "";
              res.edificeName = res.edificeName.join("/");
              return res;
            });
            let columns = this.columns.filter(function(col, index) {
              return index < 8;
            });
            console.log(columns);
            this.$refs.currentRowTable.exportCsv({
              filename: "一体机管理",
              columns: columns,
              original: false,
              data: datas
            });
            that.loading1 = false;
            this.condition.pageSize = 10;
          }
        });
    }
  }
};
</script>


